.hold_title {
  border: 1px solid #b9d8d9;
  border-bottom-color: #87bcbe;
  border-top-color: #daeaeb;
  border-radius: 5px;
  margin: 0.5em 0;
  padding-bottom: 1em;
  padding: 1em;
}

.holds_table {
  margin-bottom: 1em;
  width: 100%;
}

.holds_by_library,
.holds_by_itemtype {
  margin-bottom: 1em;
  margin-left: 1em;
}
.holds_by_library:last-child .holds_table,
.holds_by_itemtype:last-child .holds_table {
  margin-bottom: 0;
}

.holds_by_library .itemtype_holds {
  color: #5D6A8A;
}
.holds_by_library:last-child {
  margin-bottom: 0;
}